/* AccessorySelector 컴포넌트 스타일 */
.accessory-selector {
  position: relative; /* 드롭다운 목록의 위치 지정을 위함 */
  width: 100%;
}

.accessory-selector input[type="text"] {
  width: 100%;
  padding: 0px 0px;
  border: 1px solid #ced4da;
  border-radius: 4px;
  box-sizing: border-box; /* 패딩이 너비에 포함되도록 */
  font-size: 1rem;
}

.accessory-selector .dropdown-list {
  position: absolute;
  top: 100%; /* 입력 필드 바로 아래 */
  left: 0;
  right: 0;
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-height: 200px; /* 최대 높이 설정 */
  overflow-y: auto; /* 스크롤바 표시 */
  list-style: none;
  padding: 0;
  margin: 5px 0 0 0;
  z-index: 1000; /* 다른 요소 위에 표시 */
}

.accessory-selector .dropdown-list li {
  padding: 0px 0px;
  cursor: pointer;
  border-bottom: 1px solid #eee;
  display: flex; /* 자식 요소들을 가로로 정렬 */
  gap: 0px; /* 각 섹션 사이의 간격 */
  justify-content: space-between; /* 요소들 사이에 공간 균등 분배 */
  align-items: center; /* 세로 중앙 정렬 */
}

.accessory-selector .dropdown-list li:last-child {
  border-bottom: none;
}

.accessory-selector .dropdown-list li:hover {
  background-color: #f8f9fa;
}

.accessory-selector .dropdown-maker {
  flex: 2; /* 메이커 필드에 더 많은 공간 할당 */
  min-width: 80px;
  font-weight: bold;
}

.accessory-selector .dropdown-model {
  flex: 3; /* 모델명 필드에 더 많은 공간 할당 */
  min-width: 100px;
}

.accessory-selector .dropdown-spec {
  flex: 2; /* 규격 필드에 적절한 공간 할당 */
  min-width: 80px;
  text-align: right; /* 규격은 오른쪽 정렬 */
  color: #6c757d;
}

.selected-accessory-display {
  margin-top: 0px;
  font-size: 0.8em;
  color: #6c757d;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.accessory-selector .input-group-detail {
  display: flex;
  flex-direction: row; /* 명시적으로 가로 정렬 */
  gap: 0px; /* 입력 필드 간 간격 */
  margin-bottom: 0px; /* 검색 결과 드롭다운과의 간격 */
  align-items: center; /* 세로 중앙 정렬 */
  justify-content: space-between; /* 요소들 사이에 공간 균등 분배 */
}

.accessory-selector .input-group-detail input {
  flex: 1; /* 각 입력 필드가 가용한 공간을 균등하게 분배 */
}

